import axios from 'axios';
import xml2js from 'xml2js';
import {
  ALERT_CACHE_DURATION,
  REQUEST_INTERVAL
} from '../config/weatherbit';

// Map of Canadian province names to Environment Canada region codes
const PROVINCE_TO_REGION_CODE = {
  'Alberta': 'ab',
  'British Columbia': 'bc',
  'Manitoba': 'mb',
  'New Brunswick': 'nb',
  'Newfoundland and Labrador': 'nl',
  'Northwest Territories': 'nt',
  'Nova Scotia': 'ns',
  'Nunavut': 'nu',
  'Ontario': 'on',
  'Prince Edward Island': 'pe',
  'Quebec': 'qc',
  'Saskatchewan': 'sk',
  'Yukon': 'yt'
};

// CORS proxies to try when fetching alerts
const CORS_PROXIES = [
  '', // Try direct access first
  'https://thingproxy.freeboard.io/fetch/', // Most reliable based on logs
  'https://corsproxy.io/?', // Good reliability
  'https://api.codetabs.com/v1/proxy?quest=', // Decent reliability
  'https://api.allorigins.win/raw?url=', // Backup option
  'https://cors.x2u.in/', // Last resort
];

// Environment Canada RSS feed base URL
const EC_RSS_BASE_URL = 'https://weather.gc.ca/rss/warning';

// Cache for storing alerts
let alertsCache = {
  data: null,
  timestamp: null,
  lastRequestTime: null,
  locationKey: null, // Add location key to track location changes
  source: null // Track which source provided the alerts
};

// Function to clear alerts cache
export const clearAlertsCache = () => {
  alertsCache = {
    data: null,
    timestamp: null,
    lastRequestTime: null,
    locationKey: null,
    source: null
  };
};

/**
 * Get the Environment Canada region code for a province
 * @param {string} province - The province name
 * @returns {string} The region code or a default
 */
export function getRegionCodeForProvince(province) {
  if (!province) return 'on'; // Default to Ontario if no province provided
  
  // Normalize the province name by removing case sensitivity and extra spaces
  const normalizedProvince = province.trim().toLowerCase();
  
  // Check for exact matches
  for (const [key, value] of Object.entries(PROVINCE_TO_REGION_CODE)) {
    if (key.toLowerCase() === normalizedProvince) {
      return value;
    }
  }
  
  // Check for partial matches
  for (const [key, value] of Object.entries(PROVINCE_TO_REGION_CODE)) {
    if (normalizedProvince.includes(key.toLowerCase()) || key.toLowerCase().includes(normalizedProvince)) {
      return value;
    }
  }
  
  // Check for abbreviations
  const abbr = normalizedProvince.substring(0, 2);
  if (Object.values(PROVINCE_TO_REGION_CODE).includes(abbr)) {
    return abbr;
  }
  
  return 'on'; // Default to Ontario if no match found
}

/**
 * Get region codes to try for a location
 * @param {Object} locationInfo - Object containing location information
 * @returns {Array<string>} Array of region codes to try
 */
export function getRegionCodes(locationInfo) {
  const regionCodes = [];
  
  // Always include the country-level feed
  regionCodes.push('ca');
  
  // If we have province information, add that
  if (locationInfo && locationInfo.region) {
    const regionCode = getRegionCodeForProvince(locationInfo.region);
    
    // Add more specific region codes
    if (regionCode === 'on') {
      // Ontario has sub-regions
      if (locationInfo.city) {
        const cityLower = locationInfo.city.toLowerCase();
        
        // Add specific Ontario sub-regions based on the city
        if (cityLower.includes('toronto') || 
            cityLower.includes('mississauga') || 
            cityLower.includes('brampton') ||
            cityLower.includes('markham') ||
            cityLower.includes('vaughan')) {
          regionCodes.push('ongr'); // Greater Toronto
        } else if (cityLower.includes('ottawa') || 
                   cityLower.includes('gatineau') ||
                   cityLower.includes('kanata')) {
          regionCodes.push('onec'); // Eastern Ontario
        } else if (cityLower.includes('hamilton') || 
                   cityLower.includes('niagara') ||
                   cityLower.includes('st catharines')) {
          regionCodes.push('onsw'); // Southwest Ontario
        } else if (cityLower.includes('sudbury') || 
                   cityLower.includes('north bay') ||
                   cityLower.includes('timmins')) {
          regionCodes.push('onnw'); // Northwest Ontario
        } else {
          regionCodes.push('onrm'); // Rest of Ontario (fallback)
        }
      } else {
        regionCodes.push('onrm'); // Default to rest of Ontario
      }
    } else if (regionCode) {
      regionCodes.push(regionCode);
    }
  }
  
  // Make the array unique
  return [...new Set(regionCodes)];
}

/**
 * Get severity level from the alert title
 * @param {string} title - The alert title
 * @returns {string} The severity level
 */
const getSeverityLevel = (title = '') => {
  title = title.toLowerCase();
  
  // Check for severe conditions
  if (title.includes('warning') || 
      title.includes('severe') || 
      title.includes('tornado') || 
      title.includes('hurricane') ||
      title.includes('evacuation') ||
      title.includes('emergency')) {
    return 'Severe';
  }
  
  // Check for moderate conditions
  if (title.includes('watch') || 
      title.includes('advisory') ||
      title.includes('statement')) {
    return 'Moderate';
  }
  
  // Check for past conditions
  if (title.includes('ended') || 
      title.includes('cancelled') ||
      title.includes('lifted')) {
    return 'Past';
  }
  
  return 'Minor';
};

/**
 * Check if we should make a new API request based on rate limiting
 * @returns {boolean}
 */
const shouldMakeRequest = () => {
  if (!alertsCache.lastRequestTime) return true;
  const timeSinceLastRequest = Date.now() - alertsCache.lastRequestTime;
  return timeSinceLastRequest >= REQUEST_INTERVAL;
};

/**
 * Check if cached data is still valid
 * @returns {boolean}
 */
const isCacheValid = (locationInfo) => {
  if (!alertsCache.data || !alertsCache.timestamp || !alertsCache.locationKey) return false;
  
  // Check if location has changed
  const locationKey = `${locationInfo.lat},${locationInfo.lon}`;
  if (locationKey !== alertsCache.locationKey) return false;
  
  const cacheAge = Date.now() - alertsCache.timestamp;
  return cacheAge < ALERT_CACHE_DURATION;
};

/**
 * Parse XML from Environment Canada RSS feed
 * @param {string} xmlData - XML data from Environment Canada
 * @returns {Promise<Array>} Parsed alerts
 */
async function parseEnvironmentCanadaAlerts(xmlData) {
  // Create parser with more forgiving options
  const parser = new xml2js.Parser({ 
    explicitArray: false,
    normalizeTags: true,
    normalize: true,
    trim: true,
    explicitRoot: false,
    strict: false // Less strict parsing
  });
  
  try {
    // Clean up XML before parsing to handle potential issues
    const cleanedXml = xmlData
      .replace(/&(?!(amp;|lt;|gt;|quot;|apos;|#\d+;))/g, '&amp;') // Fix unescaped ampersands
      .replace(/<([^>]*)\/>/g, '<$1></\$1>'); // Convert self-closing tags to full tags
    
    const result = await parser.parseStringPromise(cleanedXml);
    
    // Handle different XML structures
    let entries = [];
    if (result.feed && result.feed.entry) {
      // ATOM format
      entries = Array.isArray(result.feed.entry) ? result.feed.entry : [result.feed.entry];
    } else if (result.rss && result.rss.channel && result.rss.channel.item) {
      // RSS format
      entries = Array.isArray(result.rss.channel.item) ? result.rss.channel.item : [result.rss.channel.item];
    } else if (result.channel && result.channel.item) {
      // RSS format without rss wrapper
      entries = Array.isArray(result.channel.item) ? result.channel.item : [result.channel.item];
    } else if (result.entry) {
      // ATOM without feed wrapper
      entries = Array.isArray(result.entry) ? result.entry : [result.entry];
    } else if (result.item) {
      // Simple RSS items
      entries = Array.isArray(result.item) ? result.item : [result.item];
    }
    
    return entries
      .filter(entry => entry && (entry.title || entry.title_))
      .map(entry => {
        // Handle different XML formats (ATOM vs RSS)
        const title = entry.title || entry.title_ || '';
        const description = entry.summary || entry.description || '';
        const link = entry.link?.$ ? entry.link.$.href : 
                    entry.link?.href ? entry.link.href : 
                    typeof entry.link === 'string' ? entry.link : 
                    'https://weather.gc.ca/warnings/index_e.html';
        const published = entry.published || entry.pubdate || entry.pubDate || new Date().toISOString();
        
        // Skip French alerts (they usually contain "en vigueur", "vigilance", or "avertissement")
        if (title.toLowerCase().includes('en vigueur') || 
            title.toLowerCase().includes('vigilance') || 
            title.toLowerCase().includes('avertissement')) {
          return null;
        }
        
        return {
          id: `ec-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
          title: title,
          description: description,
          summary: description.substring(0, 200) + (description.length > 200 ? '...' : ''),
          link: typeof link === 'string' ? link : 'https://weather.gc.ca/warnings/index_e.html',
          sent: new Date(published).toISOString(),
          expires: new Date(Date.now() + 24 * 60 * 60 * 1000).toISOString(), // Default 24h expiry
          severity: getSeverityLevel(title),
          urgency: 'Expected',
          certainty: 'Likely',
          sourceUrl: 'https://weather.gc.ca/warnings/index_e.html',
          source: 'Environment Canada' // Mark the source
        };
      })
      .filter(alert => alert !== null); // Remove null entries (French alerts)
  } catch (error) {
    console.error('Error parsing Environment Canada alerts:', error);
    return [];
  }
}

/**
 * Fetch weather alerts from Environment Canada RSS feeds
 * @param {Object} locationInfo - Object containing location information
 * @returns {Promise<Array>} Array of alert objects
 */
async function fetchEnvironmentCanadaAlerts(locationInfo) {
  if (!locationInfo) {
    console.warn('No location info provided for Environment Canada alerts');
    return [];
  }

  // Get region codes to try for this location
  const regionCodes = getRegionCodes(locationInfo);
  
  let allAlerts = [];
  let fetchedAny = false;

  // Try each region code
  for (const regionCode of regionCodes) {
    // Skip 'ca' code - use more specific ones first
    if (regionCode === 'ca' && fetchedAny) continue;

    // Try each CORS proxy until one works
    for (const proxy of CORS_PROXIES) {
      try {
        const url = `${proxy}${EC_RSS_BASE_URL}/${regionCode}_e.xml`;
        console.log(`Trying Environment Canada alerts from: ${url}`);
        
        const response = await axios.get(url, {
          headers: {
            'User-Agent': 'MapleCast Weather/1.13.0 (github.com/screech24/maplecast-weather)'
          },
          timeout: 10000 // 10 second timeout
        });
        
        if (response.status === 200 && response.data) {
          const alerts = await parseEnvironmentCanadaAlerts(response.data);
          if (alerts.length > 0) {
            allAlerts = [...allAlerts, ...alerts];
            fetchedAny = true;
            break; // Break out of proxy loop if successful
          }
        }
      } catch (error) {
        console.warn(`Failed to fetch Environment Canada alerts for region ${regionCode} using proxy ${proxy}:`, error.message);
        // Continue to next proxy
      }
    }
  }

  // Remove duplicates (same title and link)
  const uniqueAlerts = allAlerts.filter((alert, index, self) =>
    index === self.findIndex(a => a.title === alert.title && a.link === alert.link)
  );

  console.log(`Successfully fetched ${uniqueAlerts.length} Environment Canada alerts`);
  return uniqueAlerts;
}

/**
 * Fetch weather alerts
 * @param {Object} locationInfo - Object containing lat and lon
 * @returns {Promise<Array>} Array of alert objects
 */
export async function fetchWeatherAlerts(locationInfo) {
  if (!locationInfo || !locationInfo.lat || !locationInfo.lon) {
    console.warn('No location coordinates provided for fetching alerts');
    return [];
  }

  const locationKey = `${locationInfo.lat},${locationInfo.lon}`;

  // Check cache first
  if (isCacheValid(locationInfo)) {
    console.log(`Returning cached alerts from ${alertsCache.source}`);
    return alertsCache.data;
  }

  // Check rate limiting
  if (!shouldMakeRequest()) {
    console.log('Rate limit reached, returning cached data or empty array');
    return alertsCache.data || [];
  }

  let alerts = [];
  let source = 'Environment Canada';

  try {
    // Fetch from Environment Canada
    alerts = await fetchEnvironmentCanadaAlerts(locationInfo);
    console.log(`Environment Canada returned ${alerts.length} alerts`);
  } catch (error) {
    console.error('Environment Canada alerts failed:', error);
    alerts = [];
  }
  
  // Update cache
  alertsCache.data = alerts;
  alertsCache.timestamp = Date.now();
  alertsCache.lastRequestTime = Date.now();
  alertsCache.locationKey = locationKey;
  alertsCache.source = source;
  
  return alerts;
}

/**
 * Check for new alerts
 * @param {Object} locationInfo - Object containing lat and lon coordinates
 * @returns {Promise<Array>} Array of new alert objects
 */
export async function checkForNewAlerts(locationInfo) {
  // Only check if the app is active (document is visible)
  if (document.hidden) {
    console.log('App is not active, skipping alert check');
    return [];
  }

  try {
    if (!locationInfo || !locationInfo.lat || !locationInfo.lon) {
      console.log('No location coordinates available');
      return [];
    }

    const newAlerts = await fetchWeatherAlerts(locationInfo);
    const currentAlerts = alertsCache.data || [];

    // Find alerts that aren't in the current set
    const brandNewAlerts = newAlerts.filter(newAlert => 
      !currentAlerts.some(currentAlert => currentAlert.id === newAlert.id)
    );

    if (brandNewAlerts.length > 0) {
      console.log(`Found ${brandNewAlerts.length} new alerts`);
    }

    return brandNewAlerts;
  } catch (error) {
    console.error('Error checking for new alerts:', error);
    return [];
  }
}

/**
 * Register for background sync to check for alerts periodically
 * @returns {Promise<boolean>} Whether registration was successful
 */
export async function registerAlertBackgroundSync() {
  if (!('serviceWorker' in navigator) || !('SyncManager' in window)) {
    console.log('Background sync not supported');
    return false;
  }
  
  try {
    const registration = await navigator.serviceWorker.ready;
    
    // Register for background sync
    await registration.sync.register('weather-alerts-sync');
    console.log('Registered for background sync');
    
    // Register for periodic sync if supported
    if ('periodicSync' in registration) {
      try {
        // Check for permission
        const status = await navigator.permissions.query({
          name: 'periodic-background-sync',
        });
        
        if (status.state === 'granted') {
          // Register for periodic sync every 15 minutes (minimum allowed is usually 15 minutes)
          await registration.periodicSync.register('weather-alerts-periodic', {
            minInterval: 15 * 60 * 1000, // 15 minutes in milliseconds
          });
          console.log('Registered for periodic background sync');
        } else {
          console.log('Periodic background sync permission not granted');
        }
      } catch (error) {
        console.error('Error registering for periodic background sync:', error);
      }
    }
    
    return true;
  } catch (error) {
    console.error('Error registering for background sync:', error);
    return false;
  }
} 